.App {
  z-index: 0;
  text-align: center;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.bgOverlay{
  z-index: -90;
  background: radial-gradient(rgba(127, 0, 0, 0.75), rgb(0, 0, 0));
  position: fixed;
  height: 200vh;
  width: 100vw;
  margin: auto;
}

.bgOverlay2{
  z-index: -70;
  background: linear-gradient(rgba(0, 0, 127, 0.75), rgba(0, 0, 0, 0.25));
  position: fixed;
  height: 100vh;
  top: 0vh;
  width: 100vw;
  margin: auto;

}

.bgGrid {
  z-index: -100;
  position: fixed;
  left: -200vw;
  top: -32.5vh;
  height: 200vh;
  animation: scroll 20s linear infinite;
  background: url('./PortfolioBG.png');
  opacity: 100%;
  filter: blur(5px);  
  transform: none;
  -webkit-transform: perspective(100vh) rotateX(75deg);
  width: 500vw;
  
  min-width: 360px;
}

@keyframes scroll {
  100% {
    background-position: 0px -100%;
  }
}


/*Fallback if the operating system prefers reduced motion*/
@media (prefers-reduced-motion) {
  .wrapper {
    animation: scroll 180s linear infinite;
  }
} 
